import {
  ComponentRef, Injectable, TemplateRef,
  EventEmitter, Renderer2, RendererFactory2, NgZone, Injector, Optional
} from '@angular/core';
import { FEModalBackdropComponent } from './page-modal-backdrop.component';
import { FEPageModalComponent } from './page-modal.component';
import { FEPageModalSidebarComponent } from './page-sidebar.component';
import {
  FEPageModalCLASS_NAME,
  FEPModalConfigDefaults,
  FEPageModalOptions, FPageModalTRANSITION_DURATIONS
} from './page-modal-options.class';
import { FEPageModalRef } from './page-modal-ref.service';
import { Utils } from '@farris/ui-modal/utils';
import { ComponentLoaderFactory, ComponentLoader } from '@farris/ui-modal/component-loader';
import { Observable, Subject } from 'rxjs';
import { CommonUtils } from '@farris/ui-common';

declare var System: any;
@Injectable({
  providedIn: 'root'
})
export class FEPageModalService {
  version = '0.0.6';
  config: FEPageModalOptions = FEPModalConfigDefaults;

  onShow: EventEmitter<any> = new EventEmitter();
  onShown: EventEmitter<any> = new EventEmitter();
  onHide: EventEmitter<any> = new EventEmitter();
  onHidden: EventEmitter<any> = new EventEmitter();

  protected isBodyOverflowing = false;
  protected originalBodyPadding = 0;

  protected scrollbarWidth = 0;

  protected backdropRef: ComponentRef<FEModalBackdropComponent>;
  private _backdropLoader: ComponentLoader<FEModalBackdropComponent>;
  private modalsCount = 0;
  private lastDismissReason = '';

  private loaders: ComponentLoader<FEPageModalComponent | FEPageModalSidebarComponent>[] = [];

  private _renderer: Renderer2;

  private commonUtils:CommonUtils=null;

  constructor(rendererFactory: RendererFactory2, @Optional() private clf: ComponentLoaderFactory, private _injector: Injector, public ngZone: NgZone) {

    this._renderer = rendererFactory.createRenderer(null, null);
    this.commonUtils=new CommonUtils();
  }
  showByComponent() { }
  showByUrl(content: string, config?: FEPageModalOptions): Observable<FEPageModalRef | null> {
    let subject = new Subject<FEPageModalRef | null>();
    System.import(content).then((moduleFactorys: any) => {
      const moduleFactory: any = Object.values(moduleFactorys)[0];
      let moduleRef = moduleFactory.create(this._injector);
      if (moduleRef._bootstrapComponents.length > 0) {
        const entryComponent = moduleRef._bootstrapComponents[0];
        const compFactory = moduleRef.componentFactoryResolver.resolveComponentFactory(entryComponent);
        let compRef = compFactory.create(this._injector);
        let fpm: FEPageModalRef = this.show(compRef, config);
        subject.next(fpm);
      } else {
        subject.next(null);
      }
    });
    return subject;
  }
  /** Shows a modal */
  show(content: string | TemplateRef<any> | any, config?: FEPageModalOptions): FEPageModalRef {
    if (!this.clf) {
      this.clf = this._injector.get(ComponentLoaderFactory);
    }
    if (!this.clf) {
      throw new Error("FEPageModalService Can't Load ComponentLoaderFactory!");
    }
    this.modalsCount++;
    let tempConfig = { dialogType: 'modal' };
    // 
    if (config.hasOwnProperty('dialogType') && config['dialogType'] == 'sidebar') {
      tempConfig['width'] = '60%';
    }
    this.config = Object.assign({}, FEPModalConfigDefaults, tempConfig, config);
    this._createLoaders(this.config['dialogType']);
    if (config['dialogType'] != 'sidebar') {
      this.checkDialogSize();
    }

    this._showBackdrop();
    this.lastDismissReason = null;

    return this._showModal(content);
  }

  hide(level: number) {
    if (this.modalsCount === 1) {
      this._hideBackdrop();
      this.resetScrollbar();
    }
    this.modalsCount = this.modalsCount >= 1 ? this.modalsCount - 1 : 0;
    this._hideModal(level);
    this.removeLoaders(level);
  }
  /**
   * 确保当前窗口在最上层
   * @param _modalRef 
   */
  private updateZIndex(_modalRef){
    const maxZIndex=this.commonUtils.getFloatingLayerIndex();
    _modalRef.dialog.location.nativeElement.style.zIndex=maxZIndex;
  }

  _showBackdrop(): void {
    const isBackdropEnabled =
      this.config.backdrop || this.config.backdrop === 'static';
    const isBackdropInDOM =
      !this.backdropRef || !this.backdropRef.instance.isShown;

    this._backdropLoader = this.clf.createLoader<FEModalBackdropComponent>(
      null,
      null,
      this._renderer
    );

    if (this.modalsCount === 1) {
      this.removeBackdrop();

      if (isBackdropEnabled && isBackdropInDOM) {
        this._backdropLoader
          .attach(FEModalBackdropComponent)
          .to('body')
          .show({ isAnimated: this.config.animated, dialogType: this.config['dialogType'] });
        this.backdropRef = this._backdropLoader._componentRef;
      }
    }
  }

  _hideBackdrop(): void {
    if (!this.backdropRef) {
      return;
    }
    this.backdropRef.instance.isShown = false;
    const duration = this.config.animated ? FPageModalTRANSITION_DURATIONS.BACKDROP : 0;
    this.ngZone.runOutsideAngular(() => {
      setTimeout(() => this.removeBackdrop(), duration);
    });
  }

  _showModal(content: any): FEPageModalRef {
    const modalLoader = this.loaders[this.loaders.length - 1];
    const bsModalRef = new FEPageModalRef();
    // 修改showType
    this.config.initialState['showType'] = this.config.dialogType;
    const modalContainerRef = modalLoader
      .provide({ provide: FEPageModalOptions, useValue: this.config })
      .provide({ provide: FEPageModalRef, useValue: FEPageModalRef })
      .attach(this.config.dialogType == 'sidebar' ? FEPageModalSidebarComponent : FEPageModalComponent)
      .to('body')
      .show({ content, isAnimated: this.config.animated, initialState: this.config.initialState, bsModalService: this });
    modalContainerRef.instance.level = this.getModalsCount();
    modalContainerRef.instance.dialogType = this.config.dialogType;
    modalContainerRef.instance.iframe = '';
    if (this.config.dialogType === 'iframe') {
      modalContainerRef.instance.iframe = content;
    }
    bsModalRef.close = () => {
      modalContainerRef.instance.close();
    };


    // 如果是消息类型的，出现滚动条就不响应动画了
    if (!this.config.fitContent) {
      modalContainerRef.instance.toCenter();
      this.ngZone.runOutsideAngular(() => {
        document.body.addEventListener('mousedown', this._onMouseDown.bind(this));
      });
    }
    bsModalRef.content = modalLoader.getInnerComponent() || null;

    bsModalRef.dialog = modalContainerRef;

    modalContainerRef.instance['modalRef'] = bsModalRef;

    if (this.config.opened) {
      this.config.opened();
    }
    this.updateZIndex(bsModalRef);
    return bsModalRef;
  }

  _onMouseDown(e: any) {
    if (e.target.localName === 'modal-container' || e.target.classList.contains('farris-modal')) {
      this._renderer.addClass(e.target, 'animated');
      this._renderer.addClass(e.target, 'shake');
      this.ngZone.runOutsideAngular(() => {
        setTimeout(() => {
          this._renderer.removeClass(e.target, 'animated');
          this._renderer.removeClass(e.target, 'shake');
        }, 650);
      });
      return false;
    }
  }

  _hideModal(level: number): void {
    const modalLoader = this.loaders[level - 1];
    if (modalLoader) {
      modalLoader.hide();
    }
  }

  getModalsCount(): number {
    return this.modalsCount;
  }

  getCurrentModalContainer() {
    return this.loaders[this.getModalsCount() - 1];
  }

  setDismissReason(reason: string) {
    this.lastDismissReason = reason;
  }

  removeBackdrop(): void {
    if(this._backdropLoader){
      this._backdropLoader.hide();
    }
    this.backdropRef = null;
  }

  /** AFTER PR MERGE MODAL.COMPONENT WILL BE USING THIS CODE */
  /** Scroll bar tricks */
  /** @internal */
  checkScrollbar(): void {
    this.isBodyOverflowing = document.body.clientWidth < window.innerWidth;
    this.scrollbarWidth = this.getScrollbarWidth();
  }

  setScrollbar(): void {
    if (!document) {
      return;
    }

    this.originalBodyPadding = parseInt(
      window
        .getComputedStyle(document.body)
        .getPropertyValue('padding-right') || '0',
      10
    );

    if (this.isBodyOverflowing) {
      document.body.style.paddingRight = `${this.originalBodyPadding +
        this.scrollbarWidth}px`;
    }
  }

  private resetScrollbar(): void {
    document.body.style.paddingRight = `${this.originalBodyPadding}px`;
  }

  // thx d.walsh
  private getScrollbarWidth(): number {
    const scrollDiv = this._renderer.createElement('div');
    this._renderer.addClass(scrollDiv, FEPageModalCLASS_NAME.SCROLLBAR_MEASURER);
    this._renderer.appendChild(document.body, scrollDiv);
    const scrollbarWidth = scrollDiv.offsetWidth - scrollDiv.clientWidth;
    this._renderer.removeChild(document.body, scrollDiv);

    return scrollbarWidth;
  }

  private _createLoaders(dialogType = 'modal'): void {
    let loader;
    if (dialogType == 'sidebar') {
      loader = this.clf.createLoader<FEPageModalSidebarComponent>(
        null,
        null,
        this._renderer
      );
    } else {
      loader = this.clf.createLoader<FEPageModalComponent>(
        null,
        null,
        this._renderer
      );
    }

    this.copyEvent(loader.onBeforeShow, this.onShow);
    this.copyEvent(loader.onShown, this.onShown);
    this.copyEvent(loader.onBeforeHide, this.onHide);
    this.copyEvent(loader.onHidden, this.onHidden);
    this.loaders.push(loader);
  }

  private removeLoaders(level: number): void {
    this.loaders.splice(level - 1, 1);
    this.loaders.forEach(
      (loader: ComponentLoader<FEPageModalComponent | FEPageModalSidebarComponent>, i: number) => {
        loader.instance.level = i + 1;
      }
    );
  }

  private copyEvent(from: EventEmitter<any>, to: EventEmitter<any>) {
    from.subscribe(() => {
      to.emit(this.lastDismissReason);
    });
  }

  private checkDialogSize() {
    const newSize = Utils.checkDialogSize(parseInt(this.config.width + '', 10), parseInt(this.config.height+'',10));
    if (this.config.width !== newSize.width) {
      this.config.width = newSize.width - 20;
    }

    if (this.config.height !== newSize.height) {
      this.config.height = newSize.height - 20;
    }
  }
}
