import { Injectable, TemplateRef, EventEmitter } from '@angular/core';
import { Observable, of } from 'rxjs';

@Injectable()
export class FEPageModalOptions {
    /**
     *  Includes a modal-backdrop element. Alternatively,
     *  specify static for a backdrop which doesn't close the modal on click.
     */
    backdrop?: boolean | 'static';
    /**
     * Closes the modal when escape key is pressed.
     */
    keyboard?: boolean;

    focus?: boolean;
    /**
     * Shows the modal when initialized.
     */
    show?: boolean;
    /**
     * Ignore the backdrop click
     */
    ignoreBackdropClick?: boolean;
    /**
     * Css class for opened modal
     */
    class?: string;
    /**
     * Toggle animation
     */
    animated?: boolean;
    /**
     * Modal data
     */
    initialState?: object;
     /**
     * 显示关闭按钮
     */
    showCloseButton?: boolean;
    /**
     * 显示最大化按钮
     */
    showMaxButton?: boolean;
    /**
     * 显示最小化按钮
     */
    showMinButton?: boolean;

    /**
     * 窗口宽度
     */
    width?: number|string;
    /**
     * 窗口高度
     */
    height?: number|string;

    minWidth?: number;
    minHeight?: number;
    maxWidth?: number;
    maxHeight?: number;
    /** 启用拖拽功能 */
    draggable?: boolean;

    /** 启用调整尺寸大小 */
    resizable?: boolean;

    /** 是否启用滚动条 */
    enableScroll?: boolean;

    beforeOpen?: () => Observable<boolean>;
    beforeClose?: (modalRef: any) => Observable<boolean>;
    opened?: () => void;
    closed?: (isCloseButtonClick: boolean | any) => void;
    okText?: string;
    cancelText?: string;
    /** 弹窗类型：default，弹出组件、模板等；iframe,弹出iframe; ngFactory, 弹出ngFactory; sidebar:侧边栏 */
    dialogType?: string;
    // 窗口适应内容自动
    fitContent?: boolean;
    // 窗口内容区域是否自动响应式
    areaResponse?:boolean;
    sidebar?:object
}

export const FEPModalConfigDefaults: FEPageModalOptions = {
    backdrop: 'static',
    keyboard: true,
    focus: true,
    show: false,
    ignoreBackdropClick: true,
    class: '',
    animated: true,
    initialState: {},
    width: 600,
    height: 450,
    showCloseButton:true,
    minHeight: null,
    minWidth: null,
    maxHeight: null,
    maxWidth: null,
    draggable: true,
    resizable: true,
    enableScroll: true,
    beforeOpen: () => of(true),
    beforeClose: () => of(true),
    dialogType: 'modal',
    fitContent: false,
    areaResponse:true,
    sidebar:{
        showPos:'right',
        parentCls:''
    }
};

export const FEPageModalCLASS_NAME: any = {
    SCROLLBAR_MEASURER: 'modal-scrollbar-measure',
    BACKDROP: 'modal-backdrop',
    OPEN: 'modal-open',
    FADE: 'fade',
    IN: 'in', // bs3
    SHOW: 'show' // bs4
};

export const FPageModalSELECTOR: any = {
    DIALOG: '.modal-dialog',
    DATA_TOGGLE: '[data-toggle="modal"]',
    DATA_DISMISS: '[data-dismiss="modal"]',
    FIXED_CONTENT: '.navbar-fixed-top, .navbar-fixed-bottom, .is-fixed'
};

export const FPageModalTRANSITION_DURATIONS: any = {
    MODAL: 300,
    BACKDROP: 150
};

export const FPageModalDISMISS_REASONS = {
    BACKRDOP: 'backdrop-click',
    ESC: 'esc'
};
