import { Injectable, ComponentRef, QueryList } from '@angular/core';
import { FEPageModalComponent } from './page-modal.component';
import {FEPageModalSidebarComponent} from './page-sidebar.component';

@Injectable()
export class FEPageModalRef {
  /**
   * Reference to a component inside the modal. Null if modal's been created with TemplateRef
   */
  content?: any | null;
  /**
   * Hides the modal
   */
  close: () => void = Function;
  dialog?: ComponentRef<FEPageModalComponent|FEPageModalSidebarComponent> | null;
}
