import { Component, OnInit, Input, ContentChild, EventEmitter, Output, Renderer2, ElementRef, AfterContentInit, AfterViewInit, ViewChild, SimpleChanges, OnChanges, HostBinding, HostListener, OnDestroy } from '@angular/core';
import { AngularDraggableDirective } from '@farris/ui-draggable';
import { DomSanitizer, SafeResourceUrl } from "@angular/platform-browser";
import {
    FEPageModalCLASS_NAME, FPageModalDISMISS_REASONS,
    FEPageModalOptions, FPageModalTRANSITION_DURATIONS
} from './page-modal-options.class';
import { FEPageModalService } from './page-modal.service';


@Component({
    selector: 'fe-page-sidebar',
    templateUrl: './page-sidebar.component.html',
    styleUrls: [],
    host: {
        class: 'fe-cmp-page-modal'
    }
})
export class FEPageModalSidebarComponent implements OnInit, OnDestroy,AfterContentInit {
    config: FEPageModalOptions;
    isShown = false;
    level: number;
    isAnimated: boolean;
    bsModalService: FEPageModalService;
    dialogType: string;
    @Output() closed = new EventEmitter();
    @Output() opened = new EventEmitter();
    @Output() resized = new EventEmitter();
    @HostBinding('id') modalID = `MODALID_${Date.now().toPrecision()}`;
    iframeSrc: SafeResourceUrl = '';
    private _iframe = '';
    @Input()
    public set iframe(value: string) {
        if (this.sanitizer) {
            this._iframe = value;
            this.iframeSrc = this.sanitizer.bypassSecurityTrustResourceUrl(value);
        }
    }
    public get iframe() {
        return this._iframe;
    }

    @HostListener('click', ['$event'])
    onClick(event: MouseEvent) {
        const tag = event.target as any;
        if (this.config.ignoreBackdropClick || this.config.backdrop === 'static' || event.target !== this.el.nativeElement) {
            event.stopPropagation();  
            return;
        }

        this.bsModalService.setDismissReason(FPageModalDISMISS_REASONS.BACKRDOP);
        this.close();
    }

    @HostListener('window:keydown.esc', ['$event'])
    onEsc(event: any): void {
        if (!this.isShown) {
            return;
        }

        if (event.keyCode === 27) {
            event.preventDefault();
        }

        if (
            this.config.keyboard &&
            this.level === this.bsModalService.getModalsCount()
        ) {
            this.bsModalService.setDismissReason(FPageModalDISMISS_REASONS.ESC);
            this.close(event);
        }
    }
    private isModalHiding = false;

    // 侧边栏所独有
    @ViewChild('sidebarMain') private sidebarMainEl: ElementRef;
    private canRemoveInitNoAnimateCls = true;
    private parentEl: HTMLElement;
    private sidebarCls = {
        'parent': 'f-sidebar-parent',
        'openParent': 'f-sidebar-parent-expand',
        'closeParent': 'f-sidebar-parent-collapse',
        'init': 'animated-none'
    }
    isOpen = false;
    constructor(
        options: FEPageModalOptions,
        protected el: ElementRef,
        private _renderer: Renderer2,
        private sanitizer: DomSanitizer) {
        this.config = Object.assign({}, options);
    }

    ngOnInit(): void {
        this.parentEl = this.el.nativeElement.parentElement;
        this._renderer.addClass(this.parentEl, this.sidebarCls.parent);
        this.removeNoneAnimateCls();
        setTimeout(() => {
            this.isShown = true;
            this.isOpen = true;
            // this._renderer.addClass(
            //     this.el.nativeElement, FEPageModalCLASS_NAME.SHOW
            // );
            // Parent
            this._renderer.removeClass(this.parentEl, this.sidebarCls.closeParent);
            this._renderer.addClass(this.parentEl, this.sidebarCls.openParent);
            // this._renderer.setStyle(this.el.nativeElement, 'display', 'block');
            // if (this.config.fitContent && this.el.nativeElement.className.indexOf('f-modal-fitContent-scroll') > -1) {
            //   this.el.nativeElement.scrollTop = 0;
            // }

            // if (document && document.body) {
            //     const modalLen = this.bsModalService.getModalsCount();
            //     if (modalLen === 1) {
            //         this.bsModalService.checkScrollbar();
            //         this.bsModalService.setScrollbar();
            //     }
            //     if (!document.body.classList.contains(FEPageModalCLASS_NAME.OPEN)) {
            //         this._renderer.addClass(document.body, FEPageModalCLASS_NAME.OPEN);
            //     }
            // }
            this.opened.emit({ modal: this });
        }, 0);
        if (this.el.nativeElement) {
            this.el.nativeElement.focus();
        }

    }
    ngAfterContentInit() {
    }
    ngOnDestroy(): void {
        if (this.isShown) {
            this.close();
        }
    }
    hasIframe() {
        return this._iframe != '';
    }
    closeSideBar() {
        // this.removeNoneAnimateCls();
        // if (this.beforeClose) {
        //     this.beforeClose().subscribe((isOpen: boolean) => {
        //         this.isOpen = !isOpen;
        //     })
        // } else {
        //     this.isOpen = false;
        // }
        this._renderer.removeClass(this.parentEl, this.sidebarCls.openParent);
        this._renderer.addClass(this.parentEl, this.sidebarCls.closeParent);
    }
    openSideBar() {
        this.removeNoneAnimateCls();
        this.isOpen = true;
        this._renderer.removeClass(this.parentEl, this.sidebarCls.closeParent);
        this._renderer.addClass(this.parentEl, this.sidebarCls.openParent);
    }

    /*侧边栏初始因为动画收起时，会被看到, 移除 */
    private removeNoneAnimateCls() {
        if (!this.canRemoveInitNoAnimateCls) {
            return;
        }
        if (this.sidebarMainEl) {
            this._renderer.removeClass(this.sidebarMainEl.nativeElement, this.sidebarCls.init);
            this.canRemoveInitNoAnimateCls = false;
        }
    }
    // 侧边栏宽度计算
    getSidebarWidth() {
        if (typeof this.config['width'] == 'string') {
            return this.config['width'];
        } else {
            return this.config['width'] + 'px';
        }

    }
    // 必须带的方法
    close(e?: MouseEvent, isCloseButton = false): void {
        if (e) {
            e.stopPropagation();
        }
        if (this.isModalHiding || !this.isShown) {
            return;
        }

        this.config.beforeClose(this).subscribe(r => {
            if (!r) {
                return false;
            }
            //动画结束执行其他操作
            this.sidebarMainEl.nativeElement.parentElement.addEventListener('transitionend', (e: any) => {
                this.bsModalService.hide(this.level);
                this.isModalHiding = false;
                if (this.config.closed) {
                    this.config.closed(isCloseButton);
                }
                this.closed.emit(this.level);
            }, false);
            this.isModalHiding = true;
            this.isShown = false;
            this._renderer.removeClass(this.parentEl, this.sidebarCls.openParent);
            this._renderer.addClass(this.parentEl, this.sidebarCls.closeParent);
        });
    }
    toCenter() {

    }

}
